// Tabs
.nav.nav-tabs {
  margin: 0;
  border: 0;
  border-bottom: .2rem solid #e7e7e7;

  .nav-item {
    margin-bottom: -.2rem;

    .nav-link {
      padding: 1.2rem 1rem;
      border: 0;
      border-bottom: 3px solid transparent;
      color: #282d3b;
      font: 500 2.4rem / 1 $second-font-family;
      letter-spacing: .01rem;
      text-transform: uppercase;

      &:hover {
        color: $primary-color;
      }
    }

    & + .nav-item {
      margin-left: 1rem;
    }

    &.show .nav-link,
    .nav-link.active {
      border-bottom-color: $primary-color;
      color: $primary-color;
    }
  }
}

@include mq('xs', 'max') {
  .nav.nav-tabs {
    -ms-flex-pack: justify;
    justify-content: space-between;

    .nav-item + .nav-item {
      margin-left: 0;
    }
  }
}
