// Type
h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6 {
  margin-bottom: 1.8rem;
  color: $headings-text;
  font-weight: 700;
  letter-spacing: 0;
  line-height: 1.1;
}

h1, .h1 {
  font-size: 3rem;
}

h2, .h2 {
  font-size: 2.2rem;
}

h3, .h3 {
  font-size: 1.8rem;
}

h4, .h4 {
  font-size: 1.5rem;
}

h5, .h5 {
  font-size: 1.2rem;
}

h6, .h6 {
  font-size: 1rem;
}

a {
  transition: all .3s;
  color: $secondary-color;
  text-decoration: none;
}

a:hover,
a:focus {
  color: $secondary-color;
  text-decoration: underline;
}

.heading {
  margin-bottom: 4rem;
  font-size: 1.4rem;

  .title {
    margin-bottom: 1.6rem;
  }

  p {
    letter-spacing: -.015em;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.title {
  text-transform: uppercase;
}

.subtitle {
  margin-bottom: 2rem;
  color: #21293c;
  font: 700 1.9rem / 1 $font-family;
  text-transform: uppercase;
}

.carousel-title {
  margin-bottom: 2.6rem;
  border-bottom: 1px solid #e1e1e1;
  color: #302e2a;
  font: 700 1.8rem / 1 $font-family;
  letter-spacing: -.01rem;
  text-transform: uppercase;
  span{
    position: relative;
    padding: 0 0 1.2rem;
    display: inline-flex;
    &:after{
      content: "";
      position: absolute;
      width: 120%;
      height: 2px;
      background: $primary-color;
      bottom: -1px;
      left: 0;
    }
  }
}

.light-title {
  margin-bottom: 2rem;
  font-weight: 300;
}

.text-primary {
  color: $primary-color !important;
}

.lead {
  margin-bottom: 2rem;
  color: #21293c;
  font-size: 1.8rem;
  font-weight: 400;
  line-height: 1.5;
}

@include mq('md') {
  h1, .h1 {
    font-size: 3.2rem;
  }

  h2, .h2 {
    font-size: 2.4rem;
  }

  h3, .h3 {
    font-size: 2rem;
  }
}

@include mq('lg') {
  h1, .h1 {
    font-size: 3.6rem;
  }
}
